.card {
  position: relative;
  width: 214px;
  height: 200px;
  display: flex;
  flex-direction: column;
  box-shadow: 0px 15px 20px rgb(0, 0, 0, 0.05); 
  border-radius: 3px;
  border: 1px solid #f0f0f0;
}

.cardMain {
  margin: 20px 13px 20px 20px;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.cardText {
  font-size: 11px;
  color: #878787;
}

.cardTextBold {
  font-size: 13px;
  font-weight: 400;
  color: #3b2314;
}
.cardTitle {
  margin-bottom: 10px;
  margin-top: 10px;
  color: #3b2314;
  font-size: 14px;
}

.cardPrice {
  color: #3b2314;
  font-size: 20px;
  font-weight: bold;
}

.cardPriceRub {
  font-weight: 400;
}
